body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{
  width:100%;
  transition: all .5s ease-in-out
 
}
.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link{
  color:#e22104
}
.navbar-light .navbar-nav .nav-link:hover{
  color:#e22104
}
.dropdown-item:hover{
  background-color:#e22104;
  color:#fff;
  transition:all .5s ease-in-out
}
.dropdown-item{
  transition:all .5s ease-in-out;
  color:#e22104
}
.dropdown-menu{
  padding:0;
}
.dropdown:hover > .dropdown-menu{
  display: block;
}
.sticky{
  background-color: #007bff;
  position:fixed;
  width:100%;
  z-index:999;
  transition: all .5s ease-in-out
}
.Home .text , .chefP .text{
  margin-top:40px;
}
.Home .text h1 , .chefP .text h1{
  font-size: 70px;
  color:#2f2d4e;
  padding: 30px 60px 20px 0px;
}
.Home .text button , .about .text button , .offer .text button, .reserv .content button, .reser .data form button{
  background-color:#e22104;
  font-size: 14px;
  font-weight: 600;
  border-radius: 30px;
  border:none;
  padding: 10px 30px;
  color:#fff;
  margin-right: 15px;
  transition:all .5s ease-in-out;
  cursor: pointer
}
.Home .text button img{
  margin-right:10px;
}
.Home .text button:nth-of-type(2){
  background-color:#fff;
  color:#000;
  
}
.Home .text button:hover , .about .text button:hover ,.offer .text button:hover , .reserv .content button:hover, .reser .data form button:hover{
  background-color:red;
  transition: all .5s ease-in-out
}
.Home .text button:nth-of-type(2):hover{
  color:#007bff;
  background:none;
  transition:all .5s ease-in-out
}
.Home .icons , .chefP .icons{
  margin-top:80px;
  text-align:center;
  padding-right: 60px
 
}
.Home .icons img, .chefP .icons img{
  margin-bottom:15px;
}
.Home .image ,  .chefP .image{
  height:90%;
  overflow: hidden;
}
/* START about section*/  
.about{
  padding-top: 100px;
  position: relative;
}
.about .text h3{
  margin-top:30px;
  color:#e22104;
}
.about .text h3::after {
  content:"";
  display: inline-block;
  height: 0.5em;
  vertical-align: bottom;
  width: 18%;
  margin-right: -100%;
  margin-left: 10px;
  border-top: 2px solid #2f2d4e;
}
.about .text h1{
  color:#2f2d4e;
  font-family: "Josefin Sans",sans-serif;
  margin-bottom: 20px;
}
.about .text p{
  color:#888;
  line-height: 1.6em;
  margin-bottom: 45px;
}
.about .image img{
  width:70%;
  height: 100%;
}
.about .outerimg{
  width: 50%;
  height: 56%;
  position: absolute;
  top: 52%;
  left: 45%;
}
/*end style about*/
/*start feauter food */
.feature-food{
  margin-top:150px;
  margin-bottom:50px;
}
.feature-food .text h3{
  color:#e22104;
}
.feature-food .text h3::after{
  content:'';
  border-bottom: 2px solid #2f2d4e;
  vertical-align: top;
  width:10%;
  display: inline-block;
  height: 0.5em;
  margin-right: -100%;
  margin-left: 10px;
  margin-top:10px;
  
}
.feature-food .text h1 , .chef .text h1{
  color:#2f2d4e;
  margin-bottom: 80px;
  font-weight: 600;
  font-size: 50px
}
.feature-food .owl-carousel .item .innercard{
  margin-right:20px;
  transition:all .5s ease-in-out;

}
.feature-food .owl-carousel .item .innercard:hover{
  box-shadow: 0 5px 2px 3px #ddd;
  transition:all .5s ease-in-out
}
.feature-food .owl-carousel .item .innercard:hover .title{
  background-color:#fff;
  transition: all .5s ease-in-out
}
.feature-food .owl-carousel .item .innercard .title{
  padding:30px;
  background-color:rgba(255, 233, 233, .4);
  transition: all .5s ease-in-out;
  padding-bottom:10;  
}
.feature-food .owl-carousel .item .innercard .title span{
  color:#e22104;
  font-weight: bold
}
/* end feature section*/
/*start offer section*/
.offer{
  position:relative;
  background-color:rgba(255, 233, 233, .4);
  padding:120px 0;
}
.offer .text{
  background-color:#e22104;
  color:#fff;
  text-align:center;
  position: absolute;
  left: -30%;
  top: 12%;
  padding: 100px
}

.offer .text h3{
  font-size:40px;
}
.offer .text h2{
  font-size: 60px;
}
.offer .text button{
  border:1px solid #fff;
  margin-top:25px;
}
@media all and (max-width:720px){
  .offer .text{
    background-color:#e22104;
    color:#fff;
    text-align:center;
    position: relative;
    left: 0%;
    top: 0%;
    padding: 30px;
    font-size:18px
  }
  .offer .text h3{
    font-size:20px;
  }
  .offer .text h2{
    font-size: 40px;
  }
}
/*end offer section*/
/*start menu section*/
.menu{
  margin-top:50px;
}
.menu .text {
  margin-bottom: 5%
}
.menu .text h3 , .chef .text h3{
  color:#e22104
}
.menu .text h3::after , .chef .text h3::after{
  content:"";
  display: inline-block;
  height: 0.5em;
  vertical-align: bottom;
  margin-right: -100%;
  margin-left: 10px;
  border-top: 2px solid #2f2d4e;
  width:10%;
}
.menu .content {
  background-color:rgba(255, 233, 233, .4);
  padding-left: 25px;
  margin-bottom: 15px;
  transition: all .5s ease-in-out;
  padding-bottom: 15px;
  padding-top:40px;
  overflow: hidden;
}
.menu .content  img{
  margin-top:-5%
}
.menu .content .info h4{
  display:inline-block;
}
.menu .content .info h4::after{
  content:"";
  display: inline-block;
  height: 0.5em;
  vertical-align: bottom;
  width: 55%;
  margin-right: -100%;
  margin-left: 10px;
  border-top: 2px solid #2f2d4e;
}
.menu .content .info span{
  margin-left: 23%;
  color:#e22104;
  font-weight: bold;
  font-size: 20px;
  background-color:#fff
}
.menu .content .info{
  
  display:inline-block;
  margin-left: 25px;

}
.menu .content:hover{
  background-color:#fff;
  box-shadow:  0 0px 8px 5px #ddd;
  transition: all .5s ease-in-out;
  cursor: pointer
}
/* end menu*/
/*statr resevation */
.reserv{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-image:url(/Snakey/static/media/cta-bg.8f54b0ad.png);
  margin-bottom:50px;
}
.reserv .content{
  width:100%;
  height:100%;
  color:#fff;
  text-align: center;
  padding: 100px 0;
  background-color:rgba(2,1,15,0.7);
  background-attachment: fixed;
}
.reserv .content h2{
  padding:20px;
  margin-top:15px;
  margin-bottom:40px
}
/*end reserver */
/*start chef section*/
.chef .chef1 {
  background-image: url(/Snakey/static/media/chef-1.760d4a03.png);
  background-position: center top;
  height:408px;
  transition: all .5 ease-in-out;
  overflow: hidden;
  position:relative;
  margin-bottom:30px;
}
.chef .chef1 .text{
  background-color:#fff;
  text-align:center;
  margin:10px;
  position: relative;
  top:70%;
  padding:10px;
  transition: all .5 ease-in-out;
}
.chef .chef1 .text h2{
  color:#2f2d4e;
}
.chef .chef1 .text p{
  color:#888
}
.chef .chef1 .overlay{
  background-color:rgba(.8,.8,.8,.6);
  width:100%;
  height:100%;
  position: absolute;
  top: -110%;
  left:0;
  transition: all .5 ease-in-out
}
.chef .chef1:hover .overlay{
  top:0%;
  transition: all .5 ease-in-out;
}
.chef .chef1 .overlay .social{
  position: absolute;
  top:50%;
  right:0%;
  -webkit-transform:translateY(-50%);
          transform:translateY(-50%);
  font-size:20px;
}
.chef .chef1 .overlay .social i {
  display:block;
  color:#fff;
  margin-bottom:10px;
  cursor: pointer;
  margin-right:5px;

}
.chef .chef1 .overlay .social i:hover{
  color:#e22104;
  transition:all .5s ease-in-out
}
/*reser section*/
.reser{
  padding:80px 0;
}
.reser h3{
  color:#e22104;
}
.reser h3::after{
  content:"";
  display: inline-block;
  height: 0.5em;
  vertical-align: bottom;
  width: 25%;
  margin-right: -100%;
  margin-left: 10px;
  border-top: 2px solid #2f2d4e;
}
.reser h1{
    color: #2f2d4e;
