  padding-left: 10px;
}
.reser .data form button{
  position: absolute;
  top: 127%;
  left: 20%;
}
.reschef{
  margin-bottom:0;
}
/*****************************chef page ***************/
.chefP p{
  color: #888;
  font-family: "Open Sans",sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.8;
padding-right: 30px;
}
/****************Contact PAge***********/
.contact{
  margin-bottom:100px;
}
.contact h3{
  font-size: 27px;
  font-weight: 600;
  margin-bottom: 20px;
  font-family: "Josefin Sans",sans-serif;
