}
.contact textarea{
  border:1px solid #f0e9ff;
  border-radius:5px;
  outline:none;
  resize:none;
  height:200px;
  padding:10px;
  display:block;
  width:100%;
}
.contact input{
  width:47%;
  margin-top:15px;
  border:1px solid #f0e9ff;
  border-radius:5px;
  padding:15px 10px;
  margin-right:15px;
  outline:none;
}
.contact button{
  display:block;
  margin-top:30px;
  padding:10px 20px;
  border-radius:18px;
  border:none;
  background-color: #e22104;
  color:#fff;
  cursor: pointer;
}
.contact .info{
  margin-bottom:30px;
  width:100%;
}
.contact .info h3{
  margin:0;
  font-size:16px;
}
.contact i{
  margin-right:15px;
  font-size:30px;
  color:#8f9195;
}
/*footer*/
.footer{
  background-color:rgba(255, 233, 233, .4);
  padding:70px 0
}
.footer .text{
  margin-bottom:40px;
}
a{
  color:#000;
  text-decoration:none;
  transition: all .5s ease-in-out
}
a:hover{
  color:#e22104;
  text-decoration:none;
  transition:all .5s ease-in-out
}
.footer .social i{
  margin-right: 15px;
  padding:8px;
  background-color:#2f2d4e;
  color:#fff;
  transition: all .5s ease-in-out;
  cursor: pointer;
}
.footer .social i:hover{
  background-color: #e22104;
  transition: all .5s ease-in-out
}
.footer h4{
  color:#2f2d4e
}
.ares{
  margin-top:100px;
}
/*preload screen*/
.preload{
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:99999;
  background-color:#2f2d4e;
  color:#fff;
  font-family:"montserrat",sans-serif;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items:center;
          align-items:center
}
.loader {
  width:200px;
  height:200px;
  border-radius:50%;
  box-sizing:border-box;
  border-top:5px solid #e22104;
  border-bottom:5px solid #ffa801;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items:center;
          align-items:center;
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite
}
.preload::before{
  content:'';
  width:200px;
  height:200px;
  border-radius: 50%;
  border-left: 5px solid #007bff;
  box-sizing:border-box;
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.preload::after{
  content:'';
  width:200px;
  height:200px;
  border-radius: 50%;
  border-right: 5px solid #0fbcf9;
  box-sizing:border-box;
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%)
}
.loader span{
  -webkit-animation: animateText 1s linear infinite;
          animation: animateText 1s linear infinite
}

@-webkit-keyframes animate{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
  }
}

@keyframes animate{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
  }
}
@-webkit-keyframes animateText{
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg)
  }
}
@keyframes animateText{
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg)
  }
}
